.search-box {
    background-color: transparent;
    font-size: 16px;
    border: solid 0.3em white;
    display: inline-block;
    position: relative;
    border-radius: 2.5em;
  }
  .search-box input[type=text] {
    font-family: inherit;
    font-weight: bold;
    width: 2.5em;
    height: 2.5em;
    padding: 0.3em 2.1em 0.3em 0.4em;
    border: none;
    box-sizing: border-box;
    border-radius: 1.5em;
    transition: width 800ms cubic-bezier(0.68, -0.55, 0.27, 1.55) 150ms;
    background-color: transparent;
  }
  .search-box input[type=text]:focus {
    outline: none;
  }
  .search-box input[type=text]:focus, .search-box input[type=text]:not(:placeholder-shown) {
    width: 18em;
    transition: width 800ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
    color:white;
  }
  .search-box input[type=text]:focus + button[type=reset], .search-box input[type=text]:not(:placeholder-shown) + button[type=reset] {
    -webkit-transform: rotate(-45deg) translateY(0);
            transform: rotate(-45deg) translateY(0);
    transition: -webkit-transform 150ms ease-out 800ms;
    transition: transform 150ms ease-out 800ms;
    transition: transform 150ms ease-out 800ms, -webkit-transform 150ms ease-out 800ms;
  }
  .search-box input[type=text]:focus + button[type=reset]:after, .search-box input[type=text]:not(:placeholder-shown) + button[type=reset]:after {
    opacity: 1;
    transition: top 150ms ease-out 950ms, right 150ms ease-out 950ms, opacity 150ms ease 950ms;
  }
  .search-box button[type=reset] {
    background-color: transparent;
    width: 1.2em;
    height: 1.2em;
    border: 0;
    padding: 0;
    outline: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0.3em;
    right: 0.65em;
    -webkit-transform: rotate(-45deg) translateY(1.9em);
            transform: rotate(-45deg) translateY(1.9em);
    transition: -webkit-transform 150ms ease-out 150ms;
    transition: transform 150ms ease-out 150ms;
    transition: transform 150ms ease-out 150ms, -webkit-transform 150ms ease-out 150ms;
  }
  .search-box button[type=reset]:before, .search-box button[type=reset]:after {
    content: "";
    background-color:white;
    width: 0.3em;
    height: 1.4em;
    position: absolute;
  }
  .search-box button[type=reset]:after {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    opacity: 0;
    transition: opacity 150ms ease-out, -webkit-transform 150ms ease-out;
    transition: transform 150ms ease-out, opacity 150ms ease-out;
    transition: transform 150ms ease-out, opacity 150ms ease-out, -webkit-transform 150ms ease-out;
  }

  ::-webkit-input-placeholder{
    color:white;
  }

  ::placeholder{
    color:white;
  }
