.card {
  margin: 30px;
  width: 18rem;
  background: rgba(255, 255, 255, 0.3);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border-radius: 0.625;
  border-left: 1px solid rgba(255, 255, 255, 0.3);
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 10px 10px 60px -8px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
  justify-items: center;
  z-index: 2;
  overflow: hidden;
  transition: 0.3s linear
}


.card::after{
     display: block;
      /* content: ''; */
      position: absolute;
      z-index: 100;
      top: 0;
      left: 0;
      width: 100%;
      height: 120%;
      background: linear-gradient(226deg, rgba(255,255,255,0.4) 0%, rgba(255,255,255,0.4) 35%, rgba(255,255,255,0.2) 42%, rgba(255,255,255,0) 60%);
      -webkit-transform: translateY(-20%);
              transform: translateY(-20%);
      will-change: transform;
      transition: -webkit-transform .65s cubic-bezier(0.18, 0.9, 0.58, 1);
      transition: transform .65s cubic-bezier(0.18, 0.9, 0.58, 1);
      transition: transform .65s cubic-bezier(0.18, 0.9, 0.58, 1), -webkit-transform .65s cubic-bezier(0.18, 0.9, 0.58, 1);
}

.card:hover{
  
  -webkit-transform: rotateX(7deg) translateY(-6px);
  
          transform: rotateX(7deg) translateY(-6px);

  /* ::after {
    transform: translateY(0%);
  } */
}
.card::after{
      -webkit-transform: translateY(0%);
              transform: translateY(0%);
    }
    
.card::before{
  display: block;
    /* content: ''; */
    position: absolute;
    top: 5%;
    left: 5%;
    width: 90%;
    height: 90%;
    background: rgba(c_0, 0.5);
    box-shadow: 0 6px 12px 12px rgba(c_0, 0.4);
    will-change: opacity;
    -webkit-transform-origin: top center;
            transform-origin: top center;
    -webkit-transform: skewX(.001deg);
            transform: skewX(.001deg);
    transition: opacity .5s ease-in-out, -webkit-transform .35s ease-in-out;
    transition: transform .35s ease-in-out, opacity .5s ease-in-out;
    transition: transform .35s ease-in-out, opacity .5s ease-in-out, -webkit-transform .35s ease-in-out;
   
}

.card:hover::before{
  opacity: 0.6;
  -webkit-transform: rotateX(7deg) translateY(-6px) scale(1.05);
          transform: rotateX(7deg) translateY(-6px) scale(1.05);
}

